.hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0 5%;
  overflow: hidden;
  background: linear-gradient(135deg, #0c0c0c 0%, #1a1a1a 50%, #0f0f0f 100%);
}

.hero-container {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.hero-title {
  font-size: clamp(3rem, 8vw, 6rem);
  font-family: 'Georgia', 'Times New Roman', serif;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.hero-highlight {
  background: linear-gradient(135deg, #d4af37 0%, #f4d03f 50%, #d4af37 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: glow 3s ease-in-out infinite alternate;
  position: relative;
}

@keyframes glow {
  0% {
    filter: drop-shadow(0 0 10px rgba(212, 175, 55, 0.3));
  }
  100% {
    filter: drop-shadow(0 0 20px rgba(212, 175, 55, 0.6));
  }
}

.hero-subtitle {
  font-size: clamp(1.1rem, 2.5vw, 1.4rem);
  color: #cccccc;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  font-weight: 300;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.stat-item {
  text-align: center;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #d4af37;
  margin-bottom: 0.5rem;
  font-family: 'Arial', sans-serif;
}

.stat-label {
  font-size: 0.9rem;
  color: #999999;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 500;
}

.hero-actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
  flex-wrap: wrap;
}

.hero-btn {
  padding: 1rem 2.5rem;
  border: none;
  border-radius: 50px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
  letter-spacing: 0.05em;
}

.hero-btn.primary {
  background: linear-gradient(135deg, #d4af37 0%, #f4d03f 100%);
  color: #000000;
  box-shadow: 0 4px 15px rgba(212, 175, 55, 0.3);
}

.hero-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(212, 175, 55, 0.4);
}

.hero-btn.secondary {
  background: transparent;
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}

.hero-btn.secondary:hover {
  border-color: #d4af37;
  color: #d4af37;
  transform: translateY(-2px);
}

.hero-scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.scroll-arrow {
  width: 2px;
  height: 40px;
  background: linear-gradient(to bottom, #d4af37, transparent);
  position: relative;
  animation: bounce 2s infinite;
}

.scroll-arrow::before {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 10px;
  height: 10px;
  border-right: 2px solid #d4af37;
  border-bottom: 2px solid #d4af37;
  transform: translateX(-50%) rotate(45deg);
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 50%, rgba(212, 175, 55, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 70% 20%, rgba(212, 175, 55, 0.05) 0%, transparent 50%);
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.particle {
  position: absolute;
  background: rgba(212, 175, 55, 0.3);
  border-radius: 50%;
  animation: float 6s infinite ease-in-out;
}

.particle:nth-child(odd) {
  background: rgba(255, 255, 255, 0.1);
}

.particle:nth-child(1) {
  width: 4px;
  height: 4px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.particle:nth-child(2) {
  width: 6px;
  height: 6px;
  top: 60%;
  left: 20%;
  animation-delay: 1s;
}

.particle:nth-child(3) {
  width: 3px;
  height: 3px;
  top: 40%;
  left: 80%;
  animation-delay: 2s;
}

.particle:nth-child(4) {
  width: 5px;
  height: 5px;
  top: 80%;
  left: 70%;
  animation-delay: 3s;
}

.particle:nth-child(5) {
  width: 4px;
  height: 4px;
  top: 30%;
  left: 50%;
  animation-delay: 4s;
}

.particle:nth-child(6) {
  width: 6px;
  height: 6px;
  top: 70%;
  left: 40%;
  animation-delay: 5s;
}

.particle:nth-child(7) {
  width: 3px;
  height: 3px;
  top: 10%;
  left: 30%;
  animation-delay: 6s;
}

.particle:nth-child(8) {
  width: 5px;
  height: 5px;
  top: 50%;
  left: 90%;
  animation-delay: 7s;
}

.particle:nth-child(9) {
  width: 4px;
  height: 4px;
  top: 90%;
  left: 10%;
  animation-delay: 8s;
}

.particle:nth-child(10) {
  width: 6px;
  height: 6px;
  top: 20%;
  left: 70%;
  animation-delay: 9s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) translateX(0);
  }
  25% {
    transform: translateY(-20px) translateX(10px);
  }
  50% {
    transform: translateY(-10px) translateX(20px);
  }
  75% {
    transform: translateY(-30px) translateX(10px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section {
    padding: 0 1rem;
  }
  
  .hero-stats {
    gap: 2rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
  
  .hero-actions {
    flex-direction: column;
    gap: 1rem;
  }
  
  .hero-btn {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .hero-stats {
    gap: 1.5rem;
  }
  
  .stat-item {
    flex: 1;
    min-width: 100px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .stat-label {
    font-size: 0.8rem;
  }
}